import { getInfo, setInfo } from '@/utils/storage'
export default {
  namespaced: true,
  state () {
    return {
      // 个人信息获取
      userinfo: getInfo()
    }
  },
  mutations: {
    // 设置个人信息并另存本地
    setUserInfo (state, obj) {
      state.userinfo = obj
      setInfo(obj)
    }
  },
  actions: {
    // 退出登录清空用户
    logout (context) {
      context.commit('setUserInfo', {})
      context.commit('cart/setCartList', [], { root: true })
    }
  },
  getters: {}
}
