import { GetCartList, updateCartList, deleteCartList } from '@/api/cart.js'
import { Toast } from 'vant'

export default {
  namespaced: true,
  state () {
    return {
      cartList: [] // 购物车列表
    }
  },
  mutations: {
    setCartList (state, cartList) {
      state.cartList = cartList
    },
    toggleCheck (state, goodsid) {
      const goods = state.cartList.find(item => item.goods_id === goodsid)
      goods.isChecked = !goods.isChecked
    },
    // 所有全选或取消
    toggleAllCheck (state, isAllCheck) {
      state.cartList.forEach(item => { item.isChecked = isAllCheck })
    },
    changeCount (state, { goodsId, goodsNum }) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.goods_num = goodsNum
    }
  },
  actions: {
    async GetCartAction (context) {
      const { data } = await GetCartList()
      data.list.forEach(item => {
        item.isChecked = true
      })
      context.commit('setCartList', data.list)
    },
    async changeCount (context, obj) {
      const { goodsId, goodsNum, goodsSkuId } = obj
      context.commit('changeCount', { goodsId, goodsNum })
      await updateCartList(goodsId, goodsNum, goodsSkuId)
    },
    // 删除购物车数据
    async deleteCartList (context) {
      const CartList = context.getters.cartListCheck
      await deleteCartList(CartList.map(item => item.id))
      Toast('删除成功')
      // 删除成功后重新获取购物车数据
      context.dispatch('GetCartAction')
    }
  },
  getters: {
    // 商品求和
    cartaddTotal (state) {
      return state.cartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 选中的商品
    cartListCheck (state) {
      return state.cartList.filter(item => item.isChecked)
    },
    // 选中的商品总数
    cartListCheckCount (state, getters) {
      return getters.cartListCheck.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 选中的商品总价
    cartListCheckPrice (state, getters) {
      return getters.cartListCheck.reduce((sum, item) => sum + item.goods_num * item.goods.goods_price_min, 0).toFixed(2)
    },
    // 是否全选
    isAllCheck (state) {
      return state.cartList.every(item => item.isChecked)
    }
  }
}
