<template>
  <div class="cart">
    <van-nav-bar title="购物车" fixed />
    <div v-if="isLogin && cartList.length>0">
      <!-- 购物车开头 -->
      <div class="cart-title">
        <span class="all">共<i>{{ cartaddTotal }}</i>件商品</span>
        <span @click="isEdit=!isEdit" class="edit">
          <van-icon  name="edit" />
          编辑
        </span>
      </div>

      <!-- 购物车列表 -->
      <div class="cart-list" >
        <div class="cart-item" v-for="item in cartList" :key="item.goods_id">
          <van-checkbox @click="toggleCheck(item.goods_id)" :value="item.isChecked"></van-checkbox>
          <div class="show">
            <img :src="item.goods.goods_image" alt="">
          </div>
          <div class="info">
            <span class="tit text-ellipsis-2">{{ item.goods.goods_name }}</span>
            <span class="bottom">
              <div class="price">¥ <span>{{ item.goods.goods_price_min }}</span></div>
              <!-- 当需要获得实时的 :value值 需要添加剪头函数把:value转换成形参传入 -->
              <CountBox @input="(vlaue)=>changeCount(item.goods_id,vlaue,item.goods_sku_id)" :value="item.goods_num"></CountBox>
            </span>
          </div>
        </div>
      </div>

      <div class="footer-fixed">
        <div class="all-check">
          <van-checkbox @click="toggleAllCheck" :value="isAllCheck" icon-size="18"></van-checkbox>
          全选
        </div>

        <div class="all-total">
          <div class="price">
            <span>合计：</span>
            <span>¥ <i class="totalPrice">{{cartListCheckPrice}}</i></span>
          </div>
          <div v-if="!isEdit" @click="goPay()" class="goPay" :class="{disabled: cartListCheckCount === 0}">结算({{cartListCheckCount}})</div>
          <div v-else @click="handelDel" class="delete" :class="{disabled: cartListCheckCount === 0}">删除</div>
        </div>
      </div>
    </div>
    <div v-else class="empty-cart">
      <img src="@/assets/empty.png" alt="">
      <div class="tips">
        购物车是空的，先去逛逛吧~
      </div>
      <button class="btn" @click="$router.replace('/')">去逛逛</button>
    </div>
  </div>
</template>

<script>
import CountBox from '@/components/CountBox.vue'
import Bus from '@/utils/EventBus'
import { mapActions, mapState, mapGetters } from 'vuex'
export default {
  name: 'CartPage',
  components: {
    CountBox
  },
  computed: {
    ...mapState('cart', ['cartList']),
    ...mapGetters('cart', ['cartaddTotal', 'cartListCheck', 'cartListCheckCount', 'cartListCheckPrice', 'isAllCheck']),
    isLogin () {
      return this.$store.getters.token
    }
  },
  data () {
    return {
      checked: true,
      isEdit: false
    }
  },
  created () {
    this.EventBus()
    if (this.isLogin) {
      this.GetCartAction()
    }
  },
  methods: {
    ...mapActions('cart', ['GetCartAction']),
    EventBus () {
      Bus.$emit('changeActive', 2)
    },
    toggleCheck (goodsid) {
      this.$store.commit('cart/toggleCheck', goodsid)
    },
    toggleAllCheck () {
      this.$store.commit('cart/toggleAllCheck', !this.isAllCheck)
    },
    changeCount (goodsId, goodsNum, goodsSkuId) {
      this.$store.dispatch('cart/changeCount', { goodsId, goodsNum, goodsSkuId })
    },
    // 删除购物车商品
    async handelDel () {
      if (this.cartListCheck === 0) return
      await this.$store.dispatch('cart/deleteCartList')
      this.isEdit = false
    },
    // 去结算
    goPay () {
      if (this.cartListCheck.length > 0) {
        this.$router.replace({
          path: '/pay',
          query: {
            mode: 'cart',
            cartIds: this.cartListCheck.map(item => item.id).join(',')
          }
        })
      }
    }
  },
  watch: {
    isEdit (value) {
      if (value) {
        this.$store.commit('cart/toggleAllCheck', false)
      } else {
        this.$store.commit('cart/toggleAllCheck', true)
      }
    }
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.cart {
  padding-top: 46px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;
  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;
    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }
    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
  }

  .cart-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;

    .show img {
      width: 100px;
      height: 100px;
    }
    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .bottom {
        display: flex;
        justify-content: space-between;
        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;
          span {
            font-size: 16px;
          }
        }
        .count-box {
          display: flex;
          width: 110px;
          .add,
          .minus {
            width: 30px;
            height: 30px;
            outline: none;
            border: none;
          }
          .inp {
            width: 40px;
            height: 30px;
            outline: none;
            border: none;
            background-color: #efefef;
            text-align: center;
            margin: 0 5px;
          }
        }
      }
    }
  }
}

.footer-fixed {
  position: fixed;
  left: 0;
  bottom: 50px;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;

  .all-check {
    display: flex;
    align-items: center;
    .van-checkbox {
      margin-right: 5px;
    }
  }

  .all-total {
    display: flex;
    line-height: 36px;
    .price {
      font-size: 14px;
      margin-right: 10px;
      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }

    .goPay, .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;
      &.disabled {
        background-color: #ff9779;
      }
    }
  }

}

.empty-cart {
  padding: 80px 30px;
  img {
    width: 140px;
    height: 92px;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
    display: block;
    margin: 0 auto;
    border: none;
  }
}
</style>
