import request from '@/utils/request'

// 加入购物车
export const AddCartList = (goodsId, goodsNum, goodsSkuId) => {
  return request.post('/cart/add', {
    goodsId,
    goodsNum,
    goodsSkuId
  })
}

// 获取购物车列表
export const GetCartList = () => {
  return request.get('/cart/list')
}

// 更新购物车
export const updateCartList = (goodsId, goodsNum, goodsSkuId) => {
  return request.post('/cart/update',
    { goodsId, goodsNum, goodsSkuId }
  )
}

// 删除购物车
export const deleteCartList = (cartIds) => {
  return request.post('/cart/clear', { cartIds })
}
